import {
	getApplyEduPlan,
	getApply
} from '@/api/declare.js'
import store from '@/store'
export default {
	data() {
		return {
			applyid: 0,
			model: {},
			listBox: []
		}
	},
	onLoad: function(option) {
		if (option.id != undefined) {
			this.applyid = option.id
		}
	},
	onShow() {
		this.getApply();
		this.plan();
	},
	methods: {
		getApply() { 
			uni.showLoading({
				title: "加载中..."
			})
			getApply({
				token_wx: store.getters.token,
				id: this.applyid
			}).then(res => {
				this.model = res.data
				uni.hideLoading()
			})
		},
		plan() {
			uni.showLoading({
				title: "加载中..."
			})
			getApplyEduPlan({
				token_wx: store.getters.token,
				id: this.applyid
			}).then(res => {
				var item = res.data;
				var pname = item.pname.split('|');
				var data = [];

				for (var i = 0; i < pname.length; i++) {
					var pmsg = pname[i].split(',');
					data.push({
						value: pmsg[0],
						name: pmsg[1]
					})
				}
				this.listBox = data;
				uni.hideLoading()
			})
		}
	}

}
